.footer-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #90cabd;
    height: 300px;
    color: var(--primary-dark-cyan);
}

.footer-header{
    font-family: 'Barlow';
    font-weight: 900;
}

.footer-nav-items{
    list-style: none;
    display: flex;
    gap: 40px;
    padding-left: 0;
}

.footer-nav-item:hover{
    color: var(--neutral-white);
    cursor: pointer;
}

.footer-socials{
    margin-top: 50px;
    padding-left: 0;
    list-style: none;
    display: flex;
    gap: 15px;
    font-size: 24px;
    
}

.footer-social:hover{
    color: var(--neutral-white);
    cursor: pointer;
}